import React, { useState, useEffect } from "react";
import { withStyles } from "@material-ui/core/styles";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import TextField from "@material-ui/core/TextField";

const styles = theme => ({
  container: {
    display: "flex",
    flexWrap: "wrap"
  },
  textField: {
    marginLeft: theme.spacing.unit,
    marginRight: theme.spacing.unit
  },
  button: {
    float: "right",
    margin: "0 10px 10px 0"
  }
});

function MarkerForm(props) {
  const defaultErrors = {
    name: false,
    latitude: false,
    longitude: false
  };
  const { classes, currentMarker } = props;
  const [marker, setMarker] = useState(currentMarker);
  const [errors, setErrors] = useState(defaultErrors);
  const [address, setAddress] = useState("");
  const [timer, setTimer] = useState(null)

  useEffect(() => {
    setErrors(defaultErrors);
    setMarker(currentMarker);
  }, [props.currentMarker]);

  const handleInputChange = e => {
    const { name, value } = e.target;
    setMarker({ ...marker, [name]: value });
  };
  const handleAddressChange = e => {
    clearTimeout(timer);
    // bounce
    const val = e.target.value;
    setAddress(val);
    if (val.length >= 3) {
      setTimer(setTimeout(() => props.getGeocode(val), 500));
    }
  };

  const handleSubmit = e => {
    e.preventDefault();
    const err = {};
    if (!marker.name) {
      err.name = true;
    }

    if (!marker.latitude) {
      err.latitude = true;
    }

    if (!marker.longitude) {
      err.longitude = true;
    }

    if (Object.keys(err).length) {
      setErrors(err);
    } else {
      if (marker.id !== null) {
        props.updateMarker(marker);
      } else {
        props.addMarker(marker);
      }

      setAddress("");
    }
  };

  return (
    <form noValidate className={classes.container} onSubmit={handleSubmit}>
      <Grid item xs={12}>
        <TextField
          label="Search Address"
          margin="normal"
          type="address"
          placeholder="Berlin, Germany"
          fullWidth={true}
          className={classes.textField}
          onChange={handleAddressChange}
          value={address}
        />
      </Grid>
      <Grid item xs={12}>
        <TextField
          name="name"
          autoFocus={true}
          required={true}
          label="Name"
          margin="normal"
          type="name"
          className={classes.textField}
          value={marker.name}
          onChange={handleInputChange}
          error={errors.name}
        />
      </Grid>
      <Grid item xs={12} md={6}>
        <TextField
          label="Latitude"
          name="latitude"
          required={true}
          margin="normal"
          type="number"
          className={classes.textField}
          value={marker.latitude}
          onChange={handleInputChange}
          error={errors.latitude}
        />
      </Grid>

      <Grid item xs={12} md={6}>
        <TextField
          name="longitude"
          type="number"
          label="Longitude"
          required={true}
          margin="normal"
          className={classes.textField}
          value={marker.longitude}
          onChange={handleInputChange}
          error={errors.longitude}
        />
      </Grid>
      <Grid item xs={12}>
        <>
          <Button
            variant="contained"
            color="secondary"
            className={classes.button}
            onClick={() => props.setCurrentMarker()}
          >
            Cancel
          </Button>
          <Button
            type="submit"
            variant="contained"
            color="primary"
            className={classes.button}
          >
            {marker.id ? "Update" : "Create"}
          </Button>
        </>
      </Grid>
    </form>
  );
}

export default withStyles(styles)(MarkerForm);
