import React from "react";
import IconButton from "@material-ui/core/IconButton";
import DeleteIcon from "@material-ui/icons/Delete";
import EditIcon from "@material-ui/icons/Edit";
import { GridList, GridListTile } from "@material-ui/core";

function MarkerList(props) {
  return (
    <GridList cellHeight={"auto"} cols={2} className={props.className}>
      {props.markers.map(marker => (
        <GridListTile key={marker.id} cols={1}>
          <h1>{marker.name}</h1>
          <p>Latitude: {marker.latitude}</p>
          <p>Longitude: {marker.longitude}</p>
          <p>
            <IconButton aria-label="Edit" onClick={e => props.setCurrentMarker(marker)}>
              <EditIcon fontSize="large" />
            </IconButton>
            <IconButton aria-label="Delete" onClick={e => props.deleteMarker(marker.id)}>
              <DeleteIcon fontSize="large" />
            </IconButton>
          </p>
        </GridListTile>
      ))}
    </GridList>
  );
}

export default MarkerList;
