import React, { useState, useEffect, useRef } from "react";
import { Map, GoogleApiWrapper, Marker } from 'google-maps-react';

function GMaps(props) {
  const germanyCoordinates = {
   lat: 51.1657,
   lng: 10.4515
  }

  const mapRef = useRef()

  const [markers, setMarkers] = useState(props.markers)

  useEffect(()=> {
    // Pan to point only when is an existing marker
    if (props.currentMarker.id) {
      mapRef.current.map.panTo({
        lat: props.currentMarker.latitude,
        lng: props.currentMarker.longitude
      })
    }        
  }, [props.currentMarker])

  useEffect(() => {
    setMarkers(props.markers)
  }, [props.markers])

  return (
    <Map
      google={props.google}
      style={{
        width: props.width,
      }}
      zoom={6}      
      initialCenter={germanyCoordinates}
      ref={mapRef}
    >
      {markers.map(m => (
        <Marker
          key={m.id}
          name={m.name}
          position={{
            lat: m.latitude,
            lng: m.longitude
          }}
          onClick={e => props.setCurrentMarker(m)}          
        />
      ))}
    </Map>
  );
}

export default GoogleApiWrapper({
  apiKey: process.env.REACT_APP_GMAPS_KEY
})(GMaps);