import React, { useEffect } from "react";
import withRoot from "./hooks/MaterialTheme";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";
import GMaps from "./hooks/GMaps";
import MarkerForm from "./hooks/MarkerForm";
import MarkerList from "./hooks/MarkerList";
import { withStyles } from "@material-ui/core";

import { useMapitState } from "./hooks/MapitState";

const styles = theme => ({
  divider: {
    marginTop: "20px"
  },
  rightColumn: {
    width: "30%",
    float: "right",
    padding: "0 20px",
    maxHeight: "100vh"
  },
  list: {
    maxHeight: "60vh"
  }
});

const App = props => {
  const { classes } = props;
  const {
    addMarker,
    updateMarker,
    deleteMarker,
    setCurrentMarker,
    getMarkers,
    fetchMarkers,
    state,
    getGeocode
  } = useMapitState();

  useEffect(() => {
    fetchMarkers();
  }, []);

  const markers = getMarkers()

  return (
    <>
      <GMaps
        width="70%"
        markers={markers}
        setCurrentMarker={setCurrentMarker}
        currentMarker={state.currentMarker}
      />
      <Grid container spacing={0} className={classes.rightColumn}>
        <Grid item>
          <h1>Mapit</h1>
        </Grid>
        <Grid item xs={12}>
          <MarkerForm
            currentMarker={state.currentMarker}
            setCurrentMarker={setCurrentMarker}
            getGeocode={getGeocode}
            addMarker={addMarker}
            updateMarker={updateMarker}
          />
          <Divider className={classes.divider} variant="middle" />
          <MarkerList
            className={classes.list}
            markers={markers}
            setCurrentMarker={setCurrentMarker}
            deleteMarker={deleteMarker}
          />
        </Grid>
      </Grid>
    </>
  );
};

export default withRoot(withStyles(styles)(App));
