import { useCallback, useReducer } from "react";
import axios from "axios";

/**
 *
 *
 * @param {{ markers: Array }} state
 * @param {*} action
 * @returns
 */
const mapitReducer = (state, action) => {
  switch (action.type) {
    case "ADD_MARKER":
      return {
        ...state,
        markers: [action.payload, ...state.markers],
        currentMarker: emptyMarker
      };
    case "UPDATE_MARKER":
      return {
        ...state,
        markers: state.markers.map(m => m.id === action.payload.id ? action.payload : m),
        currentMarker: emptyMarker
      }
    case "DELETE_MARKER":
      return {
        ...state,
        markers: state.markers.filter(marker => marker.id !== action.payload),
        currentMarker: action.payload === state.currentMarker.id ? emptyMarker : state.currentMarker
      };
    case "SET_CURRENT_MARKER":
      return {
        ...state,
        currentMarker: action.payload
      };
    case "SET_MARKERS":
      return {
        ...state,
        markers: action.payload
      };
    default:
      return state;
  }
};

export const emptyMarker = {
  id: null,
  latitude: "",
  longitude: "",
  name: ""
};

/**
 * Returns Mapit state
 *
 * @returns { addMarker: Function, deleteMarker: Function, fetchMarkers: Function, setCurrentMarker: Function, state: any}
 */
export const useMapitState = () => {
  const [state, dispatch] = useReducer(mapitReducer, {
    currentMarker: emptyMarker,
    markers: []
  });

  const api_url = '/api/v1'

  const addMarker = useCallback(
    async marker => {
      const { data } = await axios.post(
        `${api_url}/markers`,
        marker
      );
      dispatch({ type: "ADD_MARKER", payload: data });
    },
    [dispatch]
  );

  const updateMarker = useCallback(
    async marker => {
      const { data } = await axios.put(
        `${api_url}/markers/${marker.id}`,
        marker
      );
      dispatch({ type: "UPDATE_MARKER", payload: data });
    },
    [dispatch]
  );

  const fetchMarkers = async () => {
    try {
      const { data } = await axios.get(`${api_url}/markers`,);
      dispatch({ type: "SET_MARKERS", payload: data });
    } catch (error) {
      console.log(error);
    }
  };

  const setCurrentMarker = useCallback(
    marker => dispatch({ type: "SET_CURRENT_MARKER", payload: marker || emptyMarker }),
    [state.currentMarker]
  )

  const getMarkers = useCallback(
    () => state.markers,
    [state.markers],
  )

  const getGeocode = async (address) => {
    try {
      const { data } = await axios.get(
        `${api_url}/geocoder/${address}`
      );

      if(data.results.length) {
        const first = data.results[0]
        dispatch({ 
          type: "SET_CURRENT_MARKER", 
          payload: {
            ...state.currentMarker,
            name: first.formatted_address,
            latitude: first.geometry.location.lat,
            longitude: first.geometry.location.lng
          }
        })
      }
    } catch (error) {
      console.log(error)
    }
  }

  const deleteMarker = async markerId => {
    try {
      await axios.delete(
        `${api_url}/markers/${markerId}`,
      );
      dispatch({ type: "DELETE_MARKER", payload: markerId });      
    } catch (error) {
      console.log(error)
    }
  };

  return {
    addMarker,
    updateMarker,
    deleteMarker,
    getMarkers,
    fetchMarkers,
    setCurrentMarker,
    getGeocode,
    state
  };
};
